import React from 'react'
import NavBar from 'components/navBar/NavBar'
import { InputBoxStyle , BorderReminderStyle} from './NickNameStyle'

export default (props) => {
    return (
        <div>
            <NavBar
                aligns="left"
                titleText=""
                isBack="true"
                rightAction = {props.rightAction}
            >
                
            </NavBar>
            <InputBoxStyle>
                <input placeholder="" type="text" onChange={(e)=>{props.getValue(e.target.value)}}/>
            </InputBoxStyle>
            <BorderReminderStyle>
                30 
            </BorderReminderStyle>
        </div>
    )
}