import React from 'react'
import { NavBarBorderContainer } from '../styleCollection'
import back from 'images/back.png'
import { withRouter } from 'react-router-dom'
import { Modal, Button } from 'antd-mobile'


export default withRouter((props) => {
    return (
        <NavBarBorderContainer>
            <div className="publish-back" onClick={() => handleBack(props)}>
                <img src={back} alt=""/>
            </div>
            <div className="publish-operation">
                <div className={`publish-drafts ${props.fromPage === 'publish' ? 'active' : ''}`}>
                    <App addDrafts={props.addDrafts} question="" title=""></App>
                </div>
                <div onClick={props.publish} className="publish-save">
                    
                </div>
            </div>
        </NavBarBorderContainer>
    )
})

function handleBack(props) {
    props.history.goBack()
}


const alert = Modal.alert;

const showAlert = (question, addDrafts) => {
  const alertInstance = alert('', question , [
    { text: '', onPress: () => console.log('cancel'), style: 'default' },
    { text: '', onPress: () => addDrafts() },
  ]);
  setTimeout(() => {
    // closeclose
    console.log('auto close');
    alertInstance.close();
  }, 500000);
};

const App = (props) => (
    <Button onClick={() => showAlert(props.question, props.addDrafts)}>{props.title}</Button>
)
