import React from 'react'
import {LoginContainer,LiContainer} from './StyledLogin'


export default (props)=>{
    return ( 
       <LoginContainer>
            <div>
                <header>
                    <div></div>
                </header>
                <main>
                    <ul>
                        <LiContainer>
                            <div>
                                <div className='username'><img src="./user.png" alt=""/></div>
                                <input type="text" value={props.username}  onChange={(e)=>{props.getUsername(e)}} onBlur={props.testUsername} placeholder="/" maxLength='20'/>
                            </div>
                        </LiContainer>
                        <LiContainer>
                            <div>
                                <div className='password'><img src="./lock.png" alt=""/></div>
                                <input type='password' value={props.password} onChange={(e)=>{props.getPassword(e)}} onBlur={props.testPassword} placeholder="9" maxLength='9' minLength='9'/>
                                <div className='switch' onClick={props.changeShowpassword}><img src="./switch.png" alt=""/></div>
                            </div>
                        </LiContainer>
                    </ul>
                    <div className='sure' onClick={props.submit}></div>
                    {/* <div className='forget' onClick={props.findPassword}></div> */}
                </main>
                <footer>
                    <ul>
                        <li onClick={props.loginClick} className={props.match.path==='/login'?'active':''}></li>
                        <li onClick={props.registerClick} className={props.match.path==='/register'?'active':''}></li>
                    </ul>
                </footer>
            </div>
            
       </LoginContainer>
    )
}