import React from 'react'
import NavBar from 'components/navBar/NavBar'
import SetItem from 'components/setItem/SetItem'
import { OptionStyle } from './AccessSecurityStyle'
import App from 'components/modal/Modal'

export default (props) => {
    return (
        <div>
            <NavBar 
                aligns="left"
                titleText=""
                isBack = "true"
            />
            <OptionStyle>
                <SetItem 
                    title={<App name="jahah" changeRouter={()=>{props.changeRoute('/profile/setup/verifyPwd')}} question="" title=""></App>}
                >
                </SetItem>
                <SetItem 
                    title=''
                    handleActions={()=>{props.changeRoute('/profile/setup/changePwd')}}
                />
            </OptionStyle>
        </div>
    )
}
